.workWith_cont_block1 {
    margin-left: 36px;
    max-width: 702px;
    width: 100%;
}

.work_with_pic {
    max-width: 912px;
    width: 100%;
}

.workWith_content_block {
    margin-top: 26px;
}

.workWith_content_block p {
    margin-top: 25px;
}

.workWith_cont_animation_block {
    position: relative;
    max-width: 994px;
    width: 100%;
}

.workWith_cont_animation_block img {
    max-width: 994px;
    width: 100%;
}

.workWith_cont_animation1 {
    position: absolute;
    top: 0%;
    left: -2%;
    width: 100%;
}

.workWith_cont_animation2 {
    position: absolute;
    top: 0%;
    left: 0%;
    width: 100%;
}

@media (max-width: 1440px) {
    .workWith_cont {
        margin-top: 80px;
        justify-content: center;
    }

    .workWith_cont_animation_block {
        max-width: 694px;
    }

    .workWith_cont_animation_block img {
        max-width: 694px;
    }

    .workWith_cont_block1 {
        max-width: 602px;
    }

    .workWith_cont_animation1, .workWith_cont_animation2 {
        max-width: 694px;
    }

    .workWith_cont_animation1 img, .workWith_cont_animation2 img {
        max-width: 694px;
    }
}

@media (max-width: 1200px) {
    .workWith_cont_block1 {
        margin-left: 0px;
        max-width: 700px;
    }

    .workWith_cont {
        max-width: 900px;
        width: 100%;
        margin-right: auto;
        margin-left: auto;
    }

    .workWith_cont_block1 {
        max-width: 6902px;
        width: 100%;
        margin-left: auto;
        margin-right: auto;
    }
}

@media (max-width: 540px) {
    .work_with_cont {
        /*max-width: 450px;*/
        margin-left: auto;
        margin-right: auto;
        width: 100%;
    }

    .workWith_cont_block1 {
        text-align: center;
        max-width: 450px;
    }

    .workWith_content_block p {
        margin-top: 5px;
    }

    .workWith_content_block {
        margin-top: 10px;
    }

    .workWith_cont_animation_block {
        max-width: 450px;
    }
}