.for_reviews_cont {
    margin-top: 109px;
}

.for_reviews_cont_block {
    max-width: 877px;
    width: 100%;
    margin-left: 20px;
}

.for_reviews_title2 {
    margin-top: 19px;
}

.for_reviews_content_block {
    margin-top: 22px;
}

.for_reviews_content_block p {
    margin-top: 15px;
}

.for_reviews_btn_block {
    margin-top: 55px;
}

.for_reviews_pic_animations_block {
    position: relative;
    margin-left: 50px;
    max-width: 994px;
    width: 100%;
}
.for_reviews_pic{
    max-width: 994px;
    width: 100%;
}

.for_reviews_pic_animation1 {
    position: absolute;
    top: 10%;
    left: 0%;
    width: 100%;
    /*animation-name: mymove;*/
    /*animation-duration: 1s;*/
    /*animation-fill-mode:both;*/
}

.for_reviews_pic_animation1 img {
    max-width: 994px;
    width: 100%;
}

.for_reviews_pic_animation2 {
    position: absolute;
    top: -5%;
    left: 0px;
    width: 100%;
}

.for_reviews_pic_animation2 img {
    width: 100%;
    max-width: 994px;
}

.for_reviews_pic_animation3 {
    position: absolute;
    top: 25%;
    right: -20%;
    max-width: 994px;
    width: 100%;
}

.for_reviews_pic_animation3 img {
    display: none;
}

@keyframes mymove {
    from {
        left: 30px;
    }
    to {
        left: -10%;
    }
}
@media(max-width: 1440px){
    .for_reviews_cont{
        justify-content: center;
    }
    .for_reviews_pic_animations_block {
        max-width: 594px;
        margin-left: 0px;
    }
    .for_reviews_pic {
        max-width: 594px;
    }
    .for_reviews_pic_animation1, .for_reviews_pic_animation2{
        max-width: 594px;
    }
    .for_reviews_pic_animation1 img,  .for_reviews_pic_animation2  img {
        max-width: 594px;
    }
    .for_reviews_cont_block {
        max-width: 577px;
    }
}
@media(max-width: 1300px){
    .for_reviews_cont_block {
        max-width: 507px;
    }
}
@media (max-width: 1200px) {
    .for_reviews_cont {
        margin-top: 69px;
        flex-direction: column-reverse;
        justify-content: center;
        align-items: center;
        margin-right: auto;
        margin-left: auto;
        max-width: 900px;
    }
    .for_reviews_cont_block {
        max-width: 900px;
        width: 100%;
        margin-left: 0px;
    }
    .for_reviews_pic_animations_block {
       margin-top: 50px;
        margin-left: 0px;
    }
}
@media (max-width: 768px) {
    .for_reviews_cont {
        margin-right: auto;
        margin-left: auto;
    }
}
@media(max-width: 640px){
    .for_reviews_cont {
        margin-top: 30px;
        justify-content: center;
        align-items: center;
        text-align: center;
        margin-right: auto;
        margin-left: auto;
    }
    .for_reviews_title2 {
        margin-top: 0px;
    }
}
@media(max-width: 540px){
    .for_reviews_cont{
        width: 100%;
        left: 0%;
        margin-left: auto;
        margin-right: auto;
    }
    .for_reviews_btn_block {
        margin-top: 25px;
    }
    .for_reviews_cont_block {
        max-width: 450px;
    }
    .for_reviews_pic_animations_block {
        max-width: 450px;
    }

}


