.customer_reviews_cont {
    margin-top: 190px;
}

.customer_reviews_cont_block1 {
    max-width: 797px;
    width: 100%;
    margin-right: 19px;
}

.customer_reviews_btn_block {
    margin-top: 122px;
}

.learn_more_btn {
    max-width: 240px;
    width: 100%;
}

.click_here_btn {
    max-width: 350px;
    width: 100%;
    margin-left: 27px;
}

.customer_reviews_btn_block::placeholder {
    color: #EB5C0A;
}

.customer_reviews_btn_block input {
    max-width: 300px;
    width: 100%;
    padding: 29px 16px;
    border: 1px solid #EB5C0A;
}

.start_free_btn {
    max-width: 200px;
    width: 100%;
    height: 75px;
}

.customer_reviews_animation_block {
    position: relative;
    max-width: 994px;
    width: 100%;
}

.reviews_main_pic {
    max-width: 994px;
    width: 100%;
}

.customer_reviews_pic_animation1 {
    position: absolute;
    top: -20%;
    left: 0%;
    max-width: 994px;
    width: 100%;
}

.customer_reviews_pic_animation1 img {
    max-width: 994px;
    width: 100%;
}

.customer_reviews_pic_animation2 {
    position: absolute;
    top: -20%;
    left: 0%;
    max-width: 994px;
    width: 100%;
}

.customer_reviews_pic_animation2 img {
    max-width: 994px;
    width: 100%;
}

.customer_reviews_pic_animation3 {
    position: absolute;
    top: -14%;
    left: 5%;
    max-width: 994px;
    width: 100%;
}

.customer_reviews_pic_animation3 img {
    max-width: 894px;
    width: 100%;
}

.customer_reviews_pic_animation4 {
    position: absolute;
    top: -17%;
    left: 0%;
    max-width: 994px;
    width: 100%;
}

.customer_reviews_pic_animation4 img {
    max-width: 994px;
    width: 100%;
}

.customer_reviews_pic_animation5 {
    position: absolute;
    top: -15%;
    left: 3%;
    max-width: 994px;
    width: 100%;
}

.customer_reviews_pic_animation5 img {
    max-width: 994px;
    width: 100%;
}

@media (max-width: 1440px){
    .customer_reviews_cont {
        margin-top: 150px;
        justify-content: center;
        align-items: center;
    }
    .customer_reviews_cont_block1 {
        max-width: 597px;
        width: 100%;
        margin-right: 19px;
    }
    .customer_reviews_animation_block {
        position: relative;
        max-width: 694px;
        width: 100%;
    }
    .customer_reviews_pic_animation1, .customer_reviews_pic_animation2,  .customer_reviews_pic_animation3,
    .customer_reviews_pic_animation4, .customer_reviews_pic_animation5 {
        max-width: 694px;
        width: 100%;
    }

    .customer_reviews_pic_animation1 img, .customer_reviews_pic_animation2 img,  .customer_reviews_pic_animation3 img,
    .customer_reviews_pic_animation4 img, .customer_reviews_pic_animation5 img {
        max-width: 694px;
        width: 100%;
    }
}
@media(max-width: 1300px){
    .customer_reviews_cont_block1 {
        max-width: 407px;
    }
    .customer_reviews_btn_block {
        margin-top: 62px;
    }
}
@media (max-width: 1200px) {
    .for-reviews-cont {
        margin-left: 0px;
    }
    .customer_reviews_cont_block1 {
        margin-right: 0px;
        max-width: 900px;
    }
    .customer_reviews_animation_block {
        margin-top: 120px;
    }

    .customer_reviews_pic_animation3 {
        max-width: 794px;
    }

    .customer_reviews_pic_animation5 {
        max-width: 594px;
    }

    .customer_reviews_pic_animation5 img {
        width: 100%;
    }

    .customer_reviews_cont {
        margin-top: 130px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        max-width: 900px;
        width: 100%;
        margin-right: auto;
        margin-left: auto;
    }
}
@media (max-width: 768px) {
    .reviews_main_pic {
        max-width: 595px;
    }
    .customer_reviews_pic_animation1, .customer_reviews_pic_animation2, .customer_reviews_pic_animation3,
    .customer_reviews_pic_animation4, .customer_reviews_pic_animation5 {
        width: 100%;
        left: 0%;
    }
    .customer_reviews_pic_animation1 img, .customer_reviews_pic_animation2 img, .customer_reviews_pic_animation3 img,
    .customer_reviews_pic_animation4 img, .customer_reviews_pic_animation5 img {
        width: 100%;
        left: 0%;
    }
}
@media (max-width: 540px) {
    .customer_reviews_cont{
        /*margin-top: 40px;*/
        justify-content: center;
        margin-right: auto;
        margin-left: auto;
        /*max-width: 450px;*/
    }
    .reviews_main_pic {
        max-width: 500px;
    }
    .customer_reviews_pic_animation1, .customer_reviews_pic_animation2, .customer_reviews_pic_animation3,
    .customer_reviews_pic_animation4, .customer_reviews_pic_animation5 {
        max-width: 480px;
        width: 100%;
        left: 0%;
    }
    .customer_reviews_pic_animation1 img, .customer_reviews_pic_animation2 img, .customer_reviews_pic_animation3 img,
    .customer_reviews_pic_animation4 img, .customer_reviews_pic_animation5 img {
        width: 100%;
        left: 0%;
    }
    .customer_reviews_animation_block {
        max-width: 480px;
    }
    .customer_reviews_cont_block1 {
        text-align: center;
    }
    .customer_reviews_btn_block {
        margin-top: 32px;
    }
    .customer_reviews_btn_block input {
        padding: 18px 16px;
        border: 1px solid #EB5C0A;
    }
    .start_free_btn {
        height: 55px;
    }
    .customer_reviews_animation_block {
       margin-top: 80px;
    }
}



