.home_block_2_cont {
    margin-top: 183px;
    width: 100%;
}

.home_block_2_cont_item1 {
    max-width: 585px;
    width: 100%;
    height: 300px;
    border: 1px solid #4DC7F4;
    border-top: 32px solid #4DC7F4;
}

.home_block_2_cont_item1:hover {
    border: 1px solid #EB5C0A;
    border-top: 32px solid #EB5C0A;
    transform: scale(1.03) !important;
}

.home_block_2_cont_item2 {
    max-width: 585px;
    width: 100%;
    height: 300px;
    border: 1px solid #FFBF00;
    border-top: 32px solid #FFBF00;
}

.home_block_2_cont_item2:hover {
    transform: scale(1.03) !important;
}

.home_block_2_cont_item3 {
    max-width: 585px;
    width: 100%;
    height: 300px;
    border: 1px solid #EB5C0A;
    border-top: 32px solid #EB5C0A;
}

.home_block_2_cont_item3:hover {
    border: 1px solid #4DC7F4;
    border-top: 32px solid #4DC7F4;
    transform: scale(1.03) !important;
}

.home_block_2_cont_item_inner {
    padding: 31px 90px 73px 72px;
    position: relative;
}

.home_block_2_cont_item_title {
    white-space: nowrap;
}

.home_block_2_cont_item_desc {
    margin-top: 55px;
}

.hours_weekly_pic {
    position: absolute;
    right: 2%;
    top: 45%;
}

.brand_grow_pic {
    position: absolute;
    width: 129px;
    height: 93px;
    right: 2%;
    top: 60%;
}

.market_pic {
    position: absolute;
    left: 5%;
    bottom: 7%;
}

@media (max-width: 1440px) {
    .home_block_2_cont_item_inner {
        padding: 31px 30px 43px 22px;
    }

    .hours_weekly_pic {
        right: -2%;
    }

    .brand_grow_pic {
        right: 0%;
    }

    .market_pic {
        bottom: -5%;
    }

    .home_block_2_cont_item1 {
        max-width: 485px;
    }

    .home_block_2_cont_item2 {
        max-width: 485px;
    }

    .home_block_2_cont_item3 {
        max-width: 435px;
    }

    .home_block_2_cont_item_title {
        white-space: unset;
    }

    .home_block_2_cont {
        grid-gap: 15px;
        max-width: 1300px;
        /*margin: 35px auto 0;*/
    }
}

@media (max-width: 1300px) {
    .home_block_2_cont {
        margin-top: 123px;
    }

    .home_block_2_cont_item_inner {
        padding: 21px 40px 63px 22px;
        position: relative;
        height: 200px;
    }

    .hours_weekly_pic {
        top: 65%;
        right: -5%;
    }

    .brand_grow_pic {
        top: 75%;
        right: -5%;
    }

    .market_pic {
        bottom: -30%;
    }
}

@media (max-width: 1200px) {
    .home_block_2_cont_item_inner {
        padding: 11px 90px 73px 22px;
    }

    .home_block_2_cont {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        margin-top: 70px;
        max-width: 900px;
        width: 100%;
        margin-left: auto;
        margin-right: auto;
    }

    .home_block_2_cont_item1, .home_block_2_cont_item2, .home_block_2_cont_item3 {
        width: 100%;
        max-width: 700px;
    }

    .hours_weekly_pic {
        position: absolute;
        right: 2%;
        top: 45%;
    }
    .brand_grow_pic {
        position: absolute;
        width: 129px;
        height: 93px;
        right: 2%;
        top: 60%;
    }
    .market_pic {
        bottom: -10%;
    }

}
@media(max-width: 540px){
    .market_pic {
        bottom: -30%;
    }
    .home_block_2_cont {
       max-width: 500px;
    }

}
@media(max-width: 390px){
    .home_block_2_cont_item3, .home_block_2_cont_item2,.home_block_2_cont_item1 {
        /*height:250px;*/
    }
    .home_block_2_cont_item_desc {
        margin-top: 15px;
    }
    .home_block_2_cont_item_inner {
        padding: 11px 50px 43px 22px;
    }
    .market_pic {
       display: none;
    }

}
