.reviews_management_block_reviews {
    margin-top: 35px;
    margin-bottom: 55px;
}

.reviews_management_block_reviews h2, .reviews_management_block_analytics h2 {
    margin-bottom: 10px;
}

.reviews_management_block2 {
    max-width: 1013px;
    width: 100%;
    margin-left: 76px;
}

.reviews_management_block2 .reviews_management_pic {
    max-width: 1013px;
    width: 100%;
}

@media (max-width: 1300px) {
    .reviews_management_block2 {
        max-width: 913px;
        width: 100%;
        margin-left: 26px;
    }
}

@media (max-width: 1200px) {
    .reviews_management_block2 {
        margin-top: 20px;
        margin-left: 6px;
    }

    .reviews_management_block2 .reviews_management_pic {
        max-width: 913px;
        width: 100%;
    }
}

@media (max-width: 1024px) {
    .reviews_management_block2 {
        max-width: 813px;
        width: 100%;
        display: flex;
        justify-content: center;
    }
}

@media (max-width: 900px) {
    .reviews_management_block1 {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    .reviews_management_cont{
        max-width: 700px;
        width: 100%;
        display: flex;
        justify-content: center !important;
        align-items: center;
        margin: 120px auto 80px;

    }

    .reviews_management_block2 {
        margin-left: 0px;
    }
}
@media(max-width: 768px){
    .reviews_management_cont{
        text-align: center;
        margin: 120px auto 40px;
    }
    .reviews_management_block_reviews {
        margin-top: 15px;
        margin-bottom: 25px;
    }
}

